
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

body{
    font-family: 'Roboto', sans-serif;
}

.navbar{
    background: rgb(164, 118, 118);
    height: 5rem;
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}
.logo{
    object-fit: cover;
    height: 3rem;
    width: 4rem;
}
.desktopMenuListItem{
    margin: 2rem;
    cursor: pointer;

}

.desktopMenuListItem:hover{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 2px solid teal;
}


.desktopContactImg{
  object-fit: cover;
  height: 1rem;
  width: 1rem;
  margin: 1rem;
}
.desktopContactBtn{
 background: rgb(40,40,40);
 color: white;
 border: none;
 display: flex;
 align-items: center;
 justify-content: center;
 padding: 0 1rem;
 border-radius: 2rem;

}


.active{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 2px solid yellow;
}

.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40,40,40);
    border-radius: 1rem;
}

.listItem{
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30,30 ,30);
}

.mobmenu{
    display: none;
}
@media screen and (max-width:700px) {
    .mobmenu{
        display: flex;
    }
    .desktopMenu{
        display: none;
    }
    .desktopContactBtn{
        display: none;
    }
    .desktopMenuListItem{
       font-size: 20px;
        
    }
}